import React from "react";
import ReactDOM from "react-dom";
import { Catalog, pageLoader } from "catalog";

import css from "./css/main.css";
import catalogfont from "./css/font.css";
import catalogcss from "./css/catalog.css";

const pages = [
  {
    path: "/",
    title: "Welcome",
    content: pageLoader(() => import("./html/WELCOME.md"))
  },
  {
      title: 'Brand',
      pages: [
          {
              path: '/colors',
              title: 'Colors',
              content: pageLoader(() => import("./html/brand/colors.md"))
          },
          {
              path: '/titles',
              title: 'Titles',
              content: pageLoader(() => import("./html/brand/titles.md"))
          },
          {
              path: '/paragraph',
              title: 'Paragraph',
              content: pageLoader(() => import("./html/brand/paragraph.md"))
          }
      ]
  },
  {
      title: 'Elements',
      pages: [
          {
              path: '/buttons',
              title: 'Buttons',
              content: pageLoader(() => import("./html/elements/buttons.md"))
          },
          {
              path: '/checkbox',
              title: 'Checkbox',
              content: pageLoader(() => import("./html/elements/checkbox.md"))
          },
          {
              path: '/radio',
              title: 'Radio',
              content: pageLoader(() => import("./html/elements/radio.md"))
          },
      ]
  },
  {
      title: 'Components',
      pages: [
          {
              path: '/qa',
              title: 'Components QA',
              content: pageLoader(() => import("./html/components/qa.md"))
          },
          {
              path: '/maps',
              title: 'Maps',
              content: pageLoader(() => import("./html/components/maps.md"))
          },
          {
              path: '/date-picker',
              title: 'Date Picker',
              content: pageLoader(() => import("./html/components/qa.md"))
          },
          {
              path: '/accordion',
              title: 'Accordion',
              content: pageLoader(() => import("./html/components/accordion.md"))
          }
      ]
  },
  {
      title: 'Templates',
      pages: [
          {
              path: '/templates',
              title: 'Marketing Landing Page',
              content: pageLoader(() => import("./html/templates/landing-page.md"))
          }
      ]
  },
  {
      title: 'Icons',
      pages: [
          {
              path: '/icons',
              title: 'Icons',
              content: pageLoader(() => import("./html/icons/icons.md"))
          }
      ]
  }
];

ReactDOM.render(
  <Catalog title="UI Components library" pages={pages} />,
  document.getElementById("catalog")
);



